import 'ScssComponents/your-subscription.scss';
import { exist } from '../utils/index';
import quantity from './quantity';

const yourSubscription = () => {
	const $subscriptionRoot = document.querySelector('.js-subscription');

	if (typeof ($subscriptionRoot) !== 'undefined' && $subscriptionRoot != null) {
		const subscriptionItems = $subscriptionRoot.querySelectorAll('.js-subscription-item');
		quantity();

		if (subscriptionItems.length) {
			subscriptionItems.forEach(item => {
				const $changeStateTrigger = item.querySelector('.js-acc-trigger');
				const controllerItems = item.querySelectorAll('.js-acc-element');

				let closedState = false;

				$changeStateTrigger.addEventListener('click', () => {
					if (!closedState) {
						item.classList.add('closed_state');
						closedState = true;
					} else {
						item.classList.remove('closed_state');
						closedState = false;
					}
				});

				if (controllerItems.length) {
					controllerItems.forEach(controller => {
						const $checkboxEl = controller.querySelector('.js-checkbox-element');
						const $decrementEl = controller.querySelector('.input_number_decrement');
						const $incrementEl = controller.querySelector('.input_number_increment');
						const $inputNumber = controller.querySelector('.input_number');

						let checkedState = false;

						$inputNumber.addEventListener('change', () => {
							if ($inputNumber.value > 0) {
								if (!checkedState) {
									$checkboxEl.checked = true;
									controller.classList.add('checked_state');
									checkedState = true;
									console.log(checkedState);
								}
							} else {
								$checkboxEl.checked = false;
								controller.classList.remove('checked_state');
								checkedState = false;
								console.log(checkedState);
							}
						});

						$checkboxEl.addEventListener('change', () => {
							if (!checkedState) {
								$inputNumber.value = 1;
								controller.classList.add('checked_state');
								checkedState = true;
							} else {
								$inputNumber.value = 0;
								controller.classList.remove('checked_state');
								checkedState = false;
							}
						});
					});
				}
			});
		}
	}
};

export default yourSubscription;
